.container {
  max-width: 700px;
  margin: auto;
  .itemContainer {
    margin: auto;
    margin-bottom: 100px;
    min-height: 400px;

    &:nth-of-type(1) {
      margin-top: 80px;

      img {
        @media screen and (min-width: 576px) {
          margin-top: 120px;
          transform: translateX(-30px);
        }
      }
    }

    &:nth-of-type(2) {
      h3, p {
        text-align: left !important;

        @media screen and (min-width: 576px) {
          padding-left: 50px;
        }
      }
    }

    &:nth-of-type(3) {
      h3, p {
        @media screen and (max-width: 575px) {
          text-align: left !important;
        }
      }
  
      img {
        @media screen and (min-width: 576px) {
          margin-top: 120px;
          transform: translateX(30px);
        }
      }
    }

    &:nth-of-type(4) {
      ul {
        list-style: none;
      }
      h3, p {
        @media screen and (min-width: 576px) {
          text-align: right !important;
          padding-right: 50px;
        }
      }
    }
  }

  h3 {
    font-style: normal;
    font-weight: 900;
    font-size: 36px;
    line-height: 44px;

    color: #45B5AA;

    text-shadow: 2px 2px 0px #E0E0E0;
  }

  p {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    max-width: 350px;
    margin-bottom: 50px;

    @media screen and (max-width: 991px) {
      max-width: none;
    }

    @media screen and (max-width: 575px) {
      margin-bottom: 20px;
    }

    color: #454545;
    // white-space: pre-wrap;
  }
}
